<template>
    <section class="tran-box" style="margin-bottom: 10px;">
        <h2 class="little-bar">
            {{currentCoin.pname}}/USDT
        </h2>
        <div class="tran-cont">
            <div class="flex vc">
                <label for="">{{$t('gq[1]')}}</label>
                <div class="flex1">
                    <el-radio-group v-model="form.pid">
                        <el-radio-button :label="el.pid" v-for="(el, i) in coinLists" :key='i'>{{el.pname}}</el-radio-button>
                    </el-radio-group>
                </div>
            </div>
            <div class="flex vc">
                <label for="">{{$t('gq[2]')}}</label>
                <div class="flex1">
                    <el-input :disabled="disabled"  @change="handleNum" class="el-input-numberx" style="width:100%;" type="number"  :controls="false" v-model="form.kczong" :placeholder="$t('gq[3]', {p:currentCoin.qqmin})"></el-input>
                </div>
            </div>
            <div class="flex vc">
                <label for="">{{$t('gq[4]')}}</label>
                <div class="flex1">
                    <el-radio-group v-model="form.jyzhouqi">
                        <el-radio-button :label="el" v-for="(el, i) in currentCoin.qqzq" :key="i">{{el}}s</el-radio-button>
                    </el-radio-group>
                </div>
            </div>
            <div class="flex vc" style="margin:20px auto;">
                <label for="" style="width:90px;">{{$t('gq[5]')}}:</label>
                <div class="flex1">
                    {{syrate}}%
                </div>
            </div>
            <div class="flex vc">
                <label for="">{{$t('gq[6]')}}:</label>
                <div class="flex1">
                    {{sytotal}}
                </div>
            </div>
            <div class="flex sb vc" style="margin:20px auto;">
                <el-button @click="handleBuys(1)" class="flex1" type="green">{{$t('gq[7]')}}</el-button>
                <el-button @click="handleBuys(2)" class="flex1" type="red">{{$t('gq[8]')}}</el-button>
            </div>
        </div>
        <el-dialog :title="$t('gq[14]')" :visible.sync="centerDialogVisible" width="30%" center>
            <h2 style="font-size: 20px;text-align: center">{{$t('gq[15]', {p: pstr})}}</h2>
            <div slot="footer" class="dialog-footer flex sb">
                <el-button class="flex1" @click="centerDialogVisible = false">{{$t('gq[16]')}}</el-button>
                <el-button class="flex1" type="primary" @click="handleBuy" :loading="loader">{{$t('gq[17]')}}</el-button>
            </div>
        </el-dialog>

    </section>
</template>
<script>
    import { buyQiquan, getQQCoinsLists, getQQcoin } from '@/api/contract';
    import { assetList } from '@/api/user'
    export default {
        data() {
            return {
                form: {
                    pid: '',
                    fx: '',
                    jyzhouqi:'',
                    kczong:''
                },
                loader: false,
                centerDialogVisible: false,
                coinLists: [],
                pstr:'',
                canMax: 0,
                syrate:0,
                sytotal:0,
                hasLogin: false
            }
        },
        watch: {
            // 'form.pid': function(n) {
                
            // },
            'form.jyzhouqi':function(n) {
                if(n) {
                    const index = this.currentCoin.qqzq.findIndex(el => el == n);
                    if(index != undefined) {
                        this.syrate = this.currentCoin.qqsyl[index];
                        this.checkCount(this.form.kczong);
                    }
                }
            },
            // 'form.kczong': function(n) {
            //    this.checkCount(n);
            // },

        },
        created() {
            this.hasLogin = window.sessionStorage.getItem('account');
            this.hasLogin && this.getQQCoinsLists();
        },
        computed: {
            disabled() {
                return !this.hasLogin;
            },
            currentCoin() {
                const deflt = {
                    qqsyl:[],
                    qqzq: [],
                    qqmin:0,
                    usable:0,
                    qqmax:0,
                    pname:'--'
                };
                if(this.form.pid) {
                    return this.coinLists.find(el => el.pid == this.form.pid) || deflt;
                }
                return deflt
            }
        },
        methods: {
            handleNum(n) {
                this.checkCount(n);
            },
            checkCount(n) {
                n = n || 0;
                if(Number(n)< Number(this.currentCoin.qqmin)) {
                    // this.$nextTick(() => {
                    //     this.form.kczong = Number(this.currentCoin.qqmin)
                    // })
                    return this.$message.warning('不能小于最小值'+ this.currentCoin.qqmin);
               }
               else if(Number(n) > Number(this.currentCoin.qqmax)) {
                    // this.$nextTick(() => {
                    //     this.form.kczong = Number(this.currentCoin.qqmax)
                    // })
                    return this.$message.warning('不能大于最大值'+ this.currentCoin.qqmax);
               }
               else {
                    const times = this.$public.Multiplication;
                    this.sytotal = times(Number(this.form.kczong), times(Number(this.syrate), Math.pow(10, -2))).toFixed(2)
               }
            },
            // getQQcoin() {
            //     getQQcoin({pid:this.form.pid}).then(res => {
            //         if(res.data.status == 200) {
            //             this.pcoin = res.data.data[0];
            //             this.canMax = Number(this.pcoin.usable || 0);
            //         }
            //         else {
            //             this.$message.warning(res.data.msg);
            //         }
            //     })
            // },
            getQQCoinsLists() {
                getQQCoinsLists().then(res => {
                    if(res.data.status == 200) {
                        this.coinLists = (res.data.data || []).map(el => {
                            el.qqzq = el.qqzq.split(',');
                            el.qqsyl = el.qqsyl.split(',');
                            return el;
                        })
                        //设置第一默认选择
                        if(this.coinLists.length) {
                            this.form.pid = this.coinLists[0].pid
                        }
                    }
                    else {
                        this.$message.warning(res.data.msg);
                    }
                })
            },
            doSubmit() {
                this.loader = true;
                buyQiquan({...this.form}).then(res => {
                    if(res.data.status == 200) {
                        this.centerDialogVisible = false;
                        this.$message.success(res.data.msg);
                    }else {
                        this.$message.warning(res.data.msg);
                    }
                }).finally(() => {
                    if(res.data.status == 200) {
                        this.loader = false;
                        this.$message.success(res.data.msg);
                    }
                    else {
                        this.$message.waring(res.data.msg);
                    }
                })
            },
            handleBuys(type) {
                if(!this.hasLogin) {
                    return this.$message.warning('请先登录！');
                }
                this.form.fx = type;
                if(Object.values(this.form).some(el => !el)) return;
                const n = this.form.kczong;
                if(Number(n)< Number(this.currentCoin.qqmin)) {
                    return this.$message.warning('不能小于最小值'+ this.currentCoin.qqmin);
                }
                else if(Number(n) > Number(this.currentCoin.qqmax)) {
                    return this.$message.warning('不能大于最大值'+ this.currentCoin.qqmax);
                }
                const target = this.coinLists.find(el => el.pid == this.form.pid);
                this.pstr = this.form.kczong + this.currentCoin.pname;
                this.centerDialogVisible = true
            },
            handleBuy() {
                this.doSubmit();
            },
        },
    }
</script>
<style lang="less">
    @import './index.less';
    .el-input-numberx {
       input {
        text-align: left!important;
       }
    }
</style>


