<template>
    <div class="tran-box flex1">
        <h2 class="little-bar">
                {{$t('gq[9]')}}
        </h2>
        <div class="profit-lists">
            <el-table :data="dataTable" max-height="600">
                <el-table-column prop="pname" :label="$t('gq[10]')">
                    
                </el-table-column>
                <el-table-column prop="price" :label="$t('gq[11]')">

                </el-table-column>
                <el-table-column prop="num" :label="$t('gq[12]')">

                </el-table-column>
                <el-table-column prop="time" :label="$t('gq[13]')">

                </el-table-column>
            </el-table>

        </div>
    </div>
</template>
<script>
        import {
            qiquanHistoryLists
    } from '@/api/contract'
    export default {
        data() {
            return {
                dataTable: []
            }
        },
        created() {
            this.startTicker();
        },
        beforeDestroy() {
           this.clearTicker();
        },
        methods: {
            clearTicker() {
                this.ticker && clearInterval(this.ticker)
            },
            startTicker() {
                this.clearTicker();
                this.getLists();
                this.ticker = setInterval(() => {
                    this.getLists();
                }, 2000)
            },
            getLists() {
                qiquanHistoryLists().then(res => {
                    if (res.data.status == 200) {
                        this.dataTable = res.data.data.map(el => {
                            el.time = el.time.split(' ')[1];
                            return el
                        })
                    } else {
                        this.$public.msg(r.data.msg, 'error', this)
                    }
                })
            }
        },
    }
</script>
<style lang="less">
    @import './index.less';
</style>


