<!-- 当前持仓-->
<template>
  <div class="recordTabel-wrapper">
    <el-table :data="tabelData" height="100%">
      <el-table-column prop="oid" min-width="100" :label="$t('gqlists.lishi[0]')">

      </el-table-column>
      <el-table-column prop="fx" :label="$t('gqlists.lishi[2]')">
        <template slot-scope="scope">
          <span :class="{red: scope.row.fx == 1, green: scope.row.fx == 2}">{{scope.row.fx == 1 ? '买涨' : '买跌'}}</span>
        </template>
      </el-table-column>

      <el-table-column prop="pcode" :label="$t('gqlists.lishi[3]')">

      </el-table-column>
      <el-table-column prop="kcprice" min-width="100" :label="$t('gqlists.lishi[4]')">

      </el-table-column>

      <el-table-column prop="kczong" min-width="100" :label="$t('gqlists.lishi[5]')">

      </el-table-column>
      <el-table-column prop="yqshouyilv" min-width="100" :label="$t('gqlists.lishi[6]')">
        <template slot-scope="scope">
         {{scope.row.yqshouyilv}}%
        </template>
      </el-table-column>



      <el-table-column prop="jyzhouqi" :label="$t('gqlists.lishi[7]')">
        <template slot-scope="scope">
          {{scope.row.jyzhouqi || 0}}s
        </template>
      </el-table-column>
      <!-- <el-table-column prop="dqtime" :label="$t('gqlists.lishi[8]')">

      </el-table-column> -->

      
      <el-table-column prop="zzshouyi" :label="$t('gqlists.lishi[10]')">
        
      </el-table-column>
      <el-table-column prop="kctime" min-width="160" :label="$t('gqlists.lishi[9]')">
        <template slot-scope="scope">
          {{$public.timestampToTime(scope.row.kctime)}}
        </template>
      </el-table-column>
      <el-table-column prop="status" :label="$t('gqlists.lishi[12]')">
        <template slot-scope="scope">
          <span :class="{red: scope.row.status == 3, green: scope.row.status == 2}">{{getStateText(scope.row.status)}}</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import { getQQGainLists } from "@/api/contract"
  export default {
    data() {
      return {
        tabelData: [],
      };
    },
    created() {
      this.hasLogin = window.sessionStorage.getItem('account');
      this.hasLogin && this.startTicker();
    },
    beforeDestroy() {
      this.clearTicker();
    },
    methods: {
      getStateText(status) {
        const t = {
          1:'持仓',
          2:'已盈利',
          3:'已亏损'
        }[status]
        return t || ''
      },
      clearTicker() {
        this.ticker && clearInterval(this.ticker)
      },
      startTicker() {
        this.clearTicker();
        this.getLists();
        this.ticker = setInterval(() => {
          this.getLists();
        }, 2000)
      },
      getLists() {
        getQQGainLists({status: 4}).then(res => {
          if (res.data.status == 200) {
            this.tabelData = res.data.data.map(el => {
              el.kcprice = Number(el.kcprice).toFixed(4);
              el.kczong = Number(el.kczong).toFixed(4);
              el.yqshouyi = Number(el.yqshouyi).toFixed(4);
              return el;
            })
          }
        })
      }
    }


  }

</script>
<style lang=less>
  @import "../../assets/index.less";
  @import "./recordTabel.less";
</style>


