<!-- 当前持仓-->
<template>
  <div class="recordTabel-wrapper">
    <el-table :data="tabelData" height="100%">
      <el-table-column prop="oid" min-width="100" :label="$t('gqlists.chi[0]')">

      </el-table-column>
      <el-table-column prop="fx" :label="$t('gqlists.chi[2]')">
        <template slot-scope="scope">
          <span :class="{red: scope.row.fx == 1, green: scope.row.fx == 2}">{{scope.row.fx == 1 ? '买涨' : '买跌'}}</span>
        </template>
      </el-table-column>

      <el-table-column prop="pcode" :label="$t('gqlists.chi[3]')">

      </el-table-column>
      <el-table-column prop="kcprice" min-width="100" :label="$t('gqlists.chi[4]')">

      </el-table-column>

      <el-table-column prop="kczong" min-width="100" :label="$t('gqlists.chi[5]')">

      </el-table-column>
      <el-table-column prop="yqshouyilv" min-width="100" :label="$t('gqlists.chi[6]')">
        <template slot-scope="scope">
          {{scope.row.yqshouyilv}}%
         </template>
      </el-table-column>
      <el-table-column prop="yqshouyi" :label="$t('gqlists.chi[7]')">

      </el-table-column>


      <el-table-column prop="jyzhouqi" :label="$t('gqlists.chi[8]')">
        <template slot-scope="scope">
          {{scope.row.jyzhouqi || 0}}s
        </template>
      </el-table-column>
      <el-table-column prop="dqtime" min-width="160" :label="$t('gqlists.chi[9]')">
        <template slot-scope="scope">
          {{$public.timestampToTime(scope.row.dqtime)}}
        </template>
      </el-table-column>

      <el-table-column prop="kctime" min-width="160" :label="$t('gqlists.chi[10]')">
        <template slot-scope="scope">
          {{$public.timestampToTime(scope.row.kctime)}}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import { getQQGainLists } from "@/api/contract"
  export default {
    data() {
      return {
        tabelData: [],
      };
    },
    created() {
      this.hasLogin = window.sessionStorage.getItem('account');
      this.hasLogin && this.startTicker();
    },
    beforeDestroy() {
      this.clearTicker();
    },
    methods: {
      clearTicker() {
        this.ticker && clearInterval(this.ticker)
      },
      startTicker() {
        this.clearTicker();
        this.getLists();
        this.ticker = setInterval(() => {
          this.getLists();
        }, 2000)
      },
      getLists() {
        getQQGainLists().then(res => {
          if (res.data.status == 200) {
            this.tabelData = res.data.data.map(el => {
              el.kcprice = Number(el.kcprice).toFixed(4);
              el.kczong = Number(el.kczong).toFixed(4);
              el.yqshouyi = Number(el.yqshouyi).toFixed(4);
              return el;
            })
          }
        })
      }
    }


  }

</script>
<style lang=less>
  @import "../../assets/index.less";
  @import "./recordTabel.less";
</style>


